import React, { useState, useEffect } from 'react'
import { Modal, Table } from 'react-bootstrap';
export const BuscarProductoFactura = ({ abrirModal, setAbrirModal, listProductos = [], funAddItem }) => {
    const [loading, setLoading] = useState("");
    // useEffect(() => {
    //     let datos=`
    //     const a="Soy una variable generada automaticamente";
    //     const preuba=(texto)=>{
    //         alert(texto);
    //     }
    //     setLoading("Prueba");
    //     alert(a);
    //     preuba("Dashiki");
    //     `;
    //   eval(datos);
    // }, [])

    useEffect(() => {
        if (String(loading).trim().length > 0) {
            alert(loading);
        }
    }, [loading])

    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            <Modal.Header closeButton>
                <Modal.Title as="h5">Lista de Productos</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Table striped hover responsive bordered id="tablePartidas">
                    <thead>
                        <tr>
                            <th style={{ width: "20px", textAlign: 'center' }}>Seleccionar</th>
                            <th>Codigo</th>
                            <th>Nombre</th>
                            <th>Presentación</th>
                            <th>Precio</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            listProductos.map(item => {
                                return (
                                    <tr key={`filaproducto_${item.productoId}`}>
                                        <td style={{ width: "20px", textAlign: 'center' }}>
                                            <button
                                                className="btn-icon btn btn-info btn-sm"
                                                onClick={() => { funAddItem(item) }}>
                                                <i className="feather icon-edit" /></button>
                                        </td>
                                        <td>{item.codigo}</td>
                                        <td>{item.nombreProducto}</td>
                                        <td>{item.tipoPresentacion}</td>
                                        <td>Q{Number(item.precio_venta).toFixed(2)}</td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </Table>
            </Modal.Body>
        </Modal>
    )
}
