import React, { useState, useEffect } from 'react'
import { useRef } from 'react';
import { Modal, Table } from 'react-bootstrap';
export const FacturaPrint = ({ abrirModal, setAbrirModal, data }) => {
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            <Modal.Header closeButton>
                <Modal.Title as="h5">Factura</Modal.Title>
            </Modal.Header>
            <Modal.Body style={{height:'800px'}}>
             <embed
                    type="application/pdf"
                    src={data}
                    id="pdfDocument"
                    width="100%"
                    title='Factura No.1'
                    height="100%" /> 
            </Modal.Body>
        </Modal>
    )
}
