import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import React, { useState, useEffect, useRef } from 'react'
import { Row } from 'react-bootstrap'
import { Card, Col, Button } from 'react-bootstrap'
import moment from 'moment';
import { Form } from 'react-bootstrap'
import { DropzoneComponent } from 'react-dropzone-component';
import { TextInput, ValidationForm } from 'react-bootstrap4-form-validation'
import { useSelector } from 'react-redux';
import callApi from '../../../helpers/conectorApi';
import { alert_error, alert_exitoso, alert_warning } from '../../../helpers/Notificacion'
import Aux from '../../../hoc/_Aux'
import { useForm } from '../../hooks/useForm';
import { Table } from 'react-bootstrap';
import Select from 'react-select';
import { BuscarProductoFactura } from './BuscarProductoFactura';
import { CompraAlertas } from './CompraAlertas';
import Loading from '../base/Loading';
import { ValidarNit } from '../../../helpers/ValidarNit';
import { ClienteUpSert } from './ClienteUpSert';
import { FilaDetalleFactura } from './FilaDetalleFactura';
import './Factura.css'
import NumberFormat from 'react-number-format';
import { v4 } from 'uuid';
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';
import { FacturaAlert } from './FacturaAlert';
import { FacturaPrint } from './FacturaPrint';
const menuIdSucursal = 33;
const menuIdProveedor = 29;
const menuIdCuentaBancaria = 43;
const menuIdCatalogo = 47;
const menuIdProducto = 36;
const menuIdDepartamento = 9;
let dataInicial = {
    nit: '',
    nombre: '',
    sucursalId: 0,
    observaciones: ''
}

const initDataCliente = {
    personaId: -1,
    nombre1: '',
    nombre2: '',
    apellido1: '',
    apellido2: '',
    nombreCompleto: 'CONSUMIDOR FINAL',
    nuevo: false,
    fechaNacimiento: '',
    departamentoId: 1,
    municipioId: 0,
    direccion: '',
    nit: '',
    email: '',
    estadoId: 1,
    direccionShow: ''
};

export const FacturaInsert = ({ setNuevaCompra, handleVistaPrevia }) => {
    const state = useSelector(state => state);
    const [abrirModalCliente, setAbrirModalCliente] = useState(false);
    const [catDepartamento, setCatDepartamento] = useState([]);
    const [totalSucursalesAsignadas, setTotalSucursalesAsignadas] = useState(0);
    const [abrirModalBuscarProducto, setAbrirModalBuscarProducto] = useState(false);
    const [abrirModalAlertas, setAbrirModalAlertas] = useState(false);
    const [dataAlertas, setDataAlertas] = useState({});
    const [loading, setLoading] = useState(true);
    const [accesos, setAccesos] = useState([]);
    const [listSucursales, setListSucursales] = useState([]);
    const [listProductosBuscar, setListProductosBuscar] = useState([]);
    const [listDetalleFactura, setListDetalleFactura] = useState([]);
    const [btnRegistrar, setBtnRegistrar] = useState(false);
    const [auxRenderDetalle, setAuxRenderDetalle] = useState('');
    const [valuesCliente, handleOnChangeCliente, , setValuesCliente] = useForm(initDataCliente);
    const [values, handleOnChange, , setValues] = useForm(dataInicial);
    const [dataPdf, setDataPdf] = useState();
    const [abrirModalFactura, setAbrirModalFactura] = useState(false);
    const [facturaId, setFacturaId] = useState(0);

    const [totalesDetalleFactura, setTotalesDetalleFactura] = useState({
        total: 0,
        descuento: 0,
        cambio: 0
    });
    const [valuePagaCon, handleOnChangePagaCon, , setValuesPagaCon] = useForm({
        pagaCon: 0
    });
    const [buscar, handleOnChangeBuscar, , setValuesBuscar] = useForm({
        criterio: '',
        sucursalId: 0
    });
    const GetDepartamentos = async () => {
        if (accesos.find(acceso => acceso.menuId === menuIdDepartamento && acceso.accesoId === 3)) {
            let response = await callApi('departamento?estadoId=1&include=0');
            if (response) {
                setCatDepartamento(response);
            }
        } else {
            setCatDepartamento([{ departamentoId: '', descripcion: 'No esta autorizado' }]);
        }
    }
    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuIdSucursal || item.menuId === menuIdProveedor || item.menuId === menuIdCuentaBancaria || item.menuId === menuIdCatalogo || item.menuId === menuIdProducto || item.menuId === menuIdDepartamento);
            setAccesos(misAccesos);
        }
        setLoading(false);
    }
    const GetSucursalesUsuario = async () => {
        if (accesos.find(acceso => acceso.menuId === menuIdSucursal && acceso.accesoId === 3)) {
            setLoading(true);
            let response = await callApi('usuario/sucursal/user');
            if (response) {
                let auxListaSucursales = response.map(({ sucursalId, nombre }) => {
                    let item = {
                        value: sucursalId,
                        label: nombre,
                    }
                    return item;
                })
                if (auxListaSucursales.length > 0) {
                    let { value } = auxListaSucursales[0];
                    setValues({ ...values, sucursalId: value });
                }
                setTotalSucursalesAsignadas(auxListaSucursales.length);
                setListSucursales(auxListaSucursales);
            }
        }
        setLoading(false);
    }

    const FiltrarProductos = async () => {
        if (String(buscar.criterio).trim().length > 0) {
            if (accesos.find(acceso => acceso.menuId === menuIdProducto && acceso.accesoId === 3)) {
                let response = await callApi(`producto/filtrarfact`, {
                    method: 'POST',
                    body: JSON.stringify(buscar)
                });
                if (response) {
                    if (response.length > 0) {
                        if (response.length > 1) {
                            setListProductosBuscar(response);
                            setAbrirModalBuscarProducto(true);
                        } else if (response.length === 1) {
                            InfoModalAddItem(response[0]);
                        }
                    } else {
                        alert_warning("No existen productos que cumplan con los criterios especificados")
                    }
                }
            }
            setValuesBuscar({ ...buscar, criterio: '' });
        } else {
            alert_warning("Ingrese el nombre o el código del producto")
        }
    }
    const SearchNit = async () => {
        let response = await callApi(`sat/consulta/nit/${valuesCliente.nit}`);
        if (response) {
            setValuesCliente({ ...values, ...response });
        }
    }
    const handleKeyDownNit = (e) => {
        if (e.key === 'Enter' || e.key === 'Tab') {
            let auxNit = String(valuesCliente.nit).trim().replace("-", "");
            if (String(auxNit).trim() > 0) {
                if (ValidarNit(auxNit)) {
                    SearchNit();
                } else {
                    alert_warning("El nit ingresado no es valido");
                    setValuesCliente(initDataCliente);
                }
            }
        }
    }
    const InfoModalAddItem = (item) => {
        let {
            productoId,
            codigo,
            nombreProducto,
            descripcionProducto,
            precio_venta,
            tipoPresentacion,
            unidades,
            disponible,
            tipo_presentacionId,
            descuento = 0
        } = item;
        let nuevoItems = listDetalleFactura || [];
        let existe = nuevoItems.find(i => Number(i.productoId) === Number(item.productoId));
        if (existe) {
            nuevoItems = listDetalleFactura.map((fila) => {
                if (Number(fila.productoId) === Number(item.productoId)) {
                    fila.cantidad = Number(fila.cantidad) + 1
                    fila.totalFila = Number(Number(Number(fila.precio_unitario) * Number(fila.cantidad) - Number(fila.descuento))).toFixed(2);
                }
                return fila;
            });
        } else {
            nuevoItems.push({
                bien_servicio: 'B',
                unidad_medida: 'UNI',
                productoId,
                codigo,
                nombreProducto,
                tipoPresentacion,
                tipo_presentacionId,
                cantidad: 1,
                precio_unitario: precio_venta,
                descuento: Number(descuento),
                totalFila: Number(Number(Number(1) * Number(precio_venta) - Number(descuento))).toFixed(2)
            });
        }
        setListDetalleFactura(nuevoItems);
        setAbrirModalBuscarProducto(false);
        setAuxRenderDetalle(v4());
    }
    const handleKeyDown = (e) => {
        if (e.key === 'Enter' || e.key === 'Tab') {
            FiltrarProductos();
        }
    }
    const handleDelete = (id) => {
        let filasActuales = listDetalleFactura.filter(i => Number(i.productoId) !== Number(id));
        setListDetalleFactura(filasActuales);
        setAuxRenderDetalle(v4());
    }
    const handleOnchangeSucursal = ({ value }) => {
        setValues({ ...values, sucursalId: value });
    }
    const handleUpdateFila = (event) => {
        if (event.key === 'Enter' || event.key === 'Tab' || event.key === undefined) {
            let { target } = event;
            let productoId = Number(target.id);
            let stringCantidad = String(target.value).trim();
            let nuevaCantidad = 0;
            let totalFila = 0;
            if (stringCantidad.length > 0) {
                nuevaCantidad = Number(stringCantidad);
            } else {
                nuevaCantidad = 0;
            }
            let itemsActuales = [];
            listDetalleFactura.map(filaDetalle => {
                filaDetalle.idAuxiliar = v4();
                if (Number(filaDetalle.productoId) === productoId) {
                    filaDetalle.cantidad = nuevaCantidad;
                    filaDetalle.totalFila = Number(Number(Number(nuevaCantidad) * Number(filaDetalle.precio_unitario)) - Number(filaDetalle.descuento)).toFixed(2);
                }
                console.log({ cantidad: filaDetalle.cantidad })
                if (Number(filaDetalle.cantidad) > 0) {
                    itemsActuales.push(filaDetalle);
                }
            });
            console.log(itemsActuales)
            setAuxRenderDetalle(v4());
            setListDetalleFactura(itemsActuales);
        }
    }
    const GetFacturaIf = async (id) => {
        let response = await callApi(`factura/pdf/${id}}`);
        if (response) {
            setDataPdf(response);
        }
    }
    const handleRegistrarFactura = async () => {
        let datosRequest = {};
        datosRequest.personaId = valuesCliente.personaId;
        datosRequest.sucursalId = buscar.sucursalId;
        datosRequest.cajaId = 1;
        datosRequest.observaciones = "";
        datosRequest.detalle = listDetalleFactura;
        setLoading(true);
        let response = await callApi(`factura`, {
            method: 'POST',
            body: JSON.stringify(datosRequest)
        }, false, 2);
        if (response) {
            const { code, data } = response;
            if (code === 1) {
                setFacturaId(data);
                await GetFacturaIf(data);
                const MySwal = withReactContent(Swal);
                MySwal.fire({
                    title: 'Información',
                    text: 'Factura Registrada exitosamente',
                    type: 'success',
                    showCloseButton: false,
                    showCancelButton: false
                }).then(async (willDelete) => {
                    setAbrirModalFactura(true);

                });
            } else if (code === -2 || code === -3) {
                setDataAlertas({
                    mensaje: 'Ocurrió un error al momento de registrar la factura, por las siguientes razones',
                    alerta: data
                });
                setAbrirModalAlertas(true);
            }
        }
        setLoading(false);
    }
    useEffect(() => {
        let auxNit = String(valuesCliente.nit).trim().replace("-", "");
        if (String(auxNit).trim() > 0 && abrirModalCliente === false) {
            if (ValidarNit(auxNit)) {
                SearchNit();
            }
        }
    }, [abrirModalCliente]);
    useEffect(() => {
        if (valuesCliente.nuevo === true) {
            setAbrirModalCliente(true);
        }
    }, [valuesCliente.nuevo])
    useEffect(() => {
        GetAccesosByMenuId();
    }, [state?.accesos]);
    useEffect(() => {
        GetDepartamentos();
        GetSucursalesUsuario();
    }, [accesos]);
    useEffect(() => {
        if (listSucursales.length > 0) {
            if (listSucursales.length === 1) {
                let { value } = listSucursales[0];
                setValuesBuscar({ ...buscar, sucursalId: value });
            }
        }
    }, [listSucursales]);
    useEffect(() => {
        if (values.sucursalId > 0) {
            setValuesBuscar({ ...buscar, sucursalId: values.sucursalId });
        }
    }, [values.sucursalId]);

    useEffect(() => {

        if (listDetalleFactura.length > 0) {
            let totalFinal = 0;
            let descuentoFinal = 0;
            listDetalleFactura.map(({ totalFila, descuento }) => {
                totalFinal += Number(Number(totalFila).toFixed(2));
                descuentoFinal += Number(Number(descuento).toFixed(2));
            })

            let cambio = 0;
            let { pagaCon = "" } = valuePagaCon;
            pagaCon = String(pagaCon).trim().replace("Q", "").replace(",", "");
            if (String(pagaCon).trim().length > 0) {
                cambio = Number(pagaCon) - Number(Number(totalFinal) - Number(descuentoFinal));
            }
            setTotalesDetalleFactura({
                total: totalFinal,
                descuento: descuentoFinal,
                cambio
            });

        }

    }, [listDetalleFactura, auxRenderDetalle])
    useEffect(() => {

        let { pagaCon = "" } = valuePagaCon;
        pagaCon = String(pagaCon).trim().replace("Q", "").replace(",", "");
        if (String(pagaCon).trim().length > 0) {
            let cambio = Number(pagaCon) - Number(Number(totalesDetalleFactura.total) - Number(totalesDetalleFactura.descuento));
            setTotalesDetalleFactura({
                ...totalesDetalleFactura, cambio: Number(cambio).toFixed(2)
            });
        } else {
            setValuesPagaCon({ pagaCon: 0 })
            setTotalesDetalleFactura({ ...totalesDetalleFactura, cambio: 0 });
        }
    }, [valuePagaCon])

    useEffect(() => {
        if (abrirModalFactura === false) {
            if (Number(facturaId) > 0) {
                window.location.reload();
            }
        }
    }, [abrirModalFactura])

    const errorMessage = "Campo obligatorio";
    return (
        <Aux>


            <Card>
                <Card.Header>
                    <Card.Title as="h5">Generar Factura</Card.Title>
                    <span className="d-block m-t-5">Por favor complete la información solicitada en el formulario para generar la factura</span>
                </Card.Header>
                <Card.Body>
                    {
                        loading === true ?
                            <Loading />
                            : <>
                                <h5>Información General</h5>
                                <hr></hr>
                                <Form.Row>
                                    <Form.Group as={Col} md="3">
                                        <Form.Label htmlFor="nit">Número de Nit</Form.Label>
                                        <input
                                            type="text"
                                            id="nit"
                                            name="nit"
                                            onKeyDown={handleKeyDownNit}
                                            value={valuesCliente.nit}
                                            onChange={handleOnChangeCliente}
                                            placeholder="Número de Nit"
                                            className="form-control" />
                                    </Form.Group>
                                    <Col as={Col} md="3">
                                        <Form.Label htmlFor="nit">Nombre</Form.Label>
                                        <div className="mb-3 input-group">
                                            <input
                                                type="text"
                                                id="nombre"
                                                name="nombre"
                                                value={valuesCliente.nombreCompleto}
                                                onChange={handleOnChangeCliente}
                                                placeholder="Nombre"
                                                autoComplete="off"
                                                readOnly
                                                className="form-control" />
                                            {valuesCliente.personaId > 0 &&
                                                <div className="input-group-append">
                                                    <button type="button" className="btn btn-primary" onClick={() => { setAbrirModalCliente(true) }}>Editar <i className="feather icon-edit" /></button>
                                                </div>
                                            }
                                        </div>
                                    </Col>
                                    <Col as={Col} md="6">
                                        <Form.Label htmlFor="nit">Dirección</Form.Label>
                                        <div className="mb-3 input-group">
                                            <input
                                                type="text"
                                                id="nombre"
                                                name="nombre"
                                                value={valuesCliente.direccionShow}
                                                onChange={handleOnChangeCliente}
                                                placeholder="Nombre"
                                                autoComplete="off"
                                                readOnly
                                                className="form-control" />
                                        </div>
                                    </Col>
                                </Form.Row>
                                {
                                    totalSucursalesAsignadas > 1 &&
                                    <Form.Row>
                                        <Form.Group as={Col} md="4">
                                            <Form.Label htmlFor="codigo">Sucursal</Form.Label>
                                            <Select
                                                defaultValue={listSucursales[0]}
                                                label="Single select"
                                                options={listSucursales || []}
                                                onChange={handleOnchangeSucursal}
                                            />
                                        </Form.Group>
                                    </Form.Row>
                                }
                                <br />
                                <h5>Ingreso de Producto</h5>
                                <hr></hr>
                                <Form.Row>
                                    <Col md={6}>
                                        <div className="mb-3 input-group">
                                            <input
                                                placeholder="Código o Nombre de Producto"
                                                aria-label="Código o Nombre de Producto"
                                                aria-describedby="basic-addon2"
                                                className="form-control"
                                                name="criterio"
                                                id="criterio"
                                                value={buscar.criterio}
                                                onKeyDown={handleKeyDown}
                                                onChange={handleOnChangeBuscar}
                                                autoComplete="off" />
                                            <div className="input-group-append">
                                                <button type="button" className="btn btn-primary" onClick={() => { FiltrarProductos() }}>Buscar <i className="feather icon-search" /></button>
                                            </div>
                                        </div>
                                    </Col>
                                </Form.Row>
                                {
                                    listDetalleFactura.length > 0 &&
                                    <>
                                        <h5>Detalle de compra</h5>
                                        <hr></hr>
                                        <Form.Row>
                                            <table className='table table-striped table-bordered table-hover'>
                                                <thead>
                                                    <tr>
                                                        <th style={{ textAlign: "center" }}>No.</th>
                                                        <th style={{ textAlign: "center" }}>Codigo</th>
                                                        <th style={{ textAlign: "center" }}>Nombre</th>
                                                        <th style={{ textAlign: "center" }}>Presentación</th>
                                                        <th style={{ textAlign: "center" }}>Cantidad</th>
                                                        <th style={{ textAlign: "center" }}>Precio</th>
                                                        <th style={{ textAlign: "center" }}>Descuento</th>
                                                        <th style={{ textAlign: "center" }}>Total</th>
                                                        <th style={{ textAlign: "center" }}></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {
                                                        listDetalleFactura.map((item, index) => {
                                                            return (
                                                                <FilaDetalleFactura key={`${v4()}`}
                                                                    index={index}
                                                                    idAuxRender={v4()}
                                                                    dataInicial={item}
                                                                    handleKeyPress={handleUpdateFila}
                                                                    handleDelete={handleDelete}
                                                                />
                                                            )
                                                        })
                                                    }
                                                </tbody>
                                            </table>
                                        </Form.Row>
                                        <hr></hr>
                                        <Form.Row>
                                            <Col md={3} />
                                            <Col md={3} />
                                            <Col md={3} />
                                            <Col className="text-right">
                                                <table className='table table-striped table-bordered table-hover'>
                                                    <tbody>
                                                        <tr>

                                                            <th style={{ width: 50 }}>Sub Total</th>
                                                            <td style={{ textAlign: "center" }}>Q{Number(totalesDetalleFactura.total).toFixed(2)}</td>
                                                        </tr>
                                                        <tr>
                                                            <th>Descuento</th>
                                                            <td style={{ textAlign: "center" }}>Q{Number(totalesDetalleFactura.descuento).toFixed(2)}</td>
                                                        </tr>
                                                        <tr>
                                                            <th>Total</th>
                                                            <td style={{ textAlign: "center" }}>Q{Number(Number(totalesDetalleFactura.total) - Number(totalesDetalleFactura.descuento)).toFixed(2)}</td>
                                                        </tr>
                                                        <tr>
                                                            <th>Paga Con</th>
                                                            <td style={{ width: 150 }}>
                                                                <NumberFormat
                                                                    style={{ textAlign: "center" }}
                                                                    id="pagaCon"
                                                                    name="pagaCon"
                                                                    className="form-control"
                                                                    thousandSeparator prefix="Q"
                                                                    value={valuePagaCon.pagaCon}
                                                                    onChange={handleOnChangePagaCon}
                                                                />
                                                            </td>
                                                        </tr>
                                                        <tr>
                                                            <th>Cambio</th>
                                                            <td style={{ textAlign: "center", color: totalesDetalleFactura.cambio > 0 ? "black" : "red" }}>Q{Number(Number(totalesDetalleFactura.cambio ?? 0)).toFixed(2)}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </Col>
                                        </Form.Row>

                                        <hr></hr>
                                        <Row className="align-items-center m-l-0">
                                            <Col />
                                            <Col className="text-right">
                                                {
                                                    <>
                                                        <button type="button" className="btn btn-warning mr-1" onClick={() => { setNuevaCompra(false) }}><i className="feather icon-back" />Cancelar</button>
                                                        <button type="submit" className="btn btn-success" onClick={() => { handleRegistrarFactura() }} disabled={false}><i className="feather icon-plus" />Registrar</button>
                                                    </>
                                                }
                                            </Col>
                                        </Row>
                                    </>
                                }
                            </>
                    }
                </Card.Body>
            </Card>

            {
                abrirModalCliente === true &&
                <ClienteUpSert
                    dataInicial={valuesCliente}
                    abrirModal={abrirModalCliente}
                    setAbrirModal={setAbrirModalCliente}
                    catDepartamento={catDepartamento}
                />
            }
            {
                abrirModalBuscarProducto === true &&
                <BuscarProductoFactura
                    abrirModal={abrirModalBuscarProducto}
                    setAbrirModal={setAbrirModalBuscarProducto}
                    listProductos={listProductosBuscar}
                    funAddItem={InfoModalAddItem} />
            }
            {
                abrirModalAlertas === true &&
                <FacturaAlert
                    abrirModal={abrirModalAlertas}
                    setAbrirModal={setAbrirModalAlertas}
                    data={dataAlertas}
                />
            }
            {
                abrirModalFactura === true &&
                <FacturaPrint
                    abrirModal={abrirModalFactura}
                    setAbrirModal={setAbrirModalFactura}
                    data={dataPdf}
                />
            }
        </Aux>
    )
}
