import React, { useState } from 'react'
import { Card } from 'react-bootstrap'
import Aux from '../../../hoc/_Aux'
import { useFormDinamik } from '../../hooks/useForm'
import { v4 as uuidv4 } from 'uuid';
import { useEffect } from 'react';
export const FormularioDinamico = () => {
    return (
        <Aux>

            <Card>
                <Card.Header>
                    <Card.Title as="h5">Generar Factura</Card.Title>
                    <span className="d-block m-t-5">Por favor complete la información solicitada en el formulario para generar la factura</span>
                </Card.Header>
                <Card.Body>
                    <form noValidate="" className="needs-validation">
                        <Panel key={`mipanel${uuidv4}`} />
                        <MyButton />
                    </form>
                </Card.Body>
                <Card.Footer>
                </Card.Footer>
            </Card>
        </Aux>
    )
}

