const MyInput = ({ values, handleOnChange, inputProperties }) => {
    const [properties, setProperties] = useState(inputProperties);

    return (
        <div {...properties.div}>
            <label {...properties.label}>{properties.label.text}</label>
            <input
                {...properties.input}
                onChange={handleOnChange}
                value={values[properties.input.id]}
            />

        </div>
    )
}
const MyListen = ({ values, handleOnChange, inputProperties }) => {
    const [properties, setProperties] = useState(inputProperties);
    return (
        <div {...properties.div}>
            <label {...properties.label}>{properties.label.text}</label>
            <div>
                <select
                    {...properties.list}
                    onChange={handleOnChange}
                    value={values[properties.list.id]}
                >
                    {
                        properties.items.map(({ value, text }) => {
                            return (
                                <option value={value} key={value}>{text}</option>
                            )
                        })
                    }
                </select>
                <div className="invalid-feedback" style={{ display: 'block' }}>{properties.errorMessage}</div>
            </div>
        </div>
    )
}

const MyButton = ({ values }) => {
    const handleInput = (e) => {
        e.preventDefault();
    }
    return (
        <div className="align-items-center m-l-0 row">
            <div className="col">
            </div>
            <div className="text-right col">
                <button
                    type="button"
                    className="btn btn-warning mr-1">
                    <i className="feather icon-back"></i>Cancelar
                </button>
                <button
                    type="submit"
                    className="btn btn-success"
                    onChange={() => { handleInput() }}>
                    <i className="feather icon-plus"></i>Registrar
                </button>
            </div>
        </div>
    )
}

