import React, { useState, useEffect } from 'react'
import { Modal } from 'react-bootstrap';
import { alert_error, alert_warning } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';
import { ValidationForm, TextInput, SelectGroup } from 'react-bootstrap4-form-validation';
import { Col, Form } from 'react-bootstrap';
import { Alert, Table } from 'react-bootstrap';
export const FacturaAlert = ({ abrirModal, setAbrirModal, data = {} }) => {
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            <Modal.Header closeButton>
                <Modal.Title as="h5">Alertas</Modal.Title>
            </Modal.Header>
            <Modal.Body>

                <Alert dismissible={true} variant="warning">
                    {data?.mensaje}
                </Alert>
                <Table responsive>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Código</th>
                            <th>Alerta</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data?.alerta.map((item, index) => {
                                return (
                                    <tr key={`codigo_alerta${index}`}>
                                        <td>{index+1}</td>
                                        <td>{item.CodigoError}</td>
                                        <td>{item.DescripcionError}</td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </Table>
            </Modal.Body>
        </Modal>
    )
}
