import React, { useState, useEffect } from 'react'
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import NumberFormat from 'react-number-format'
import { useForm } from '../../hooks/useForm';
import { alert_warning } from '../../../helpers/Notificacion';
import { Form } from 'react-bootstrap';
export const FilaDetalleFactura = ({ index,dataInicial,handleKeyPress,handleDelete }) => {

    return (
        <tr>
            <td style={{ textAlign: "center" }}>
                {index+1}
            </td>
            <td style={{ textAlign: "center" }}>{dataInicial.codigo}</td>
            <td style={{ textAlign: "center" }}>{dataInicial.nombreProducto}</td>
            <td style={{ textAlign: "center" }}>{dataInicial.tipoPresentacion}</td>
            <td style={{width:150}}>
                <NumberFormat
                    style={{ textAlign: "center" }}
                    id={`${dataInicial.productoId}`}
                    name="cantidad"
                    className="form-control input-cantidad"
                    thousandSeparator prefix=""
                    value={dataInicial.cantidad}
                    onKeyDown={handleKeyPress}
                    onBlur={handleKeyPress}
                />
            </td>
            <td style={{ textAlign: "center" }}>Q{Number(dataInicial.precio_unitario).toFixed(2)}</td>
            <td style={{ textAlign: "center" }}>Q{Number(dataInicial.descuento).toFixed(2)}</td>
            <td style={{ textAlign: "center" }}>Q{Number(dataInicial.totalFila).toFixed(2)}</td>
            <td style={{ textAlign: "center" }}>
                <a href={"#"} onClick={() => {handleDelete(dataInicial.productoId) }}><i className="feather icon-trash-2 f-w-600 f-16 text-c-red" /></a>
            </td>
        </tr>
    )
}
