import moment from 'moment';
import React, { useState, useEffect } from 'react'
import { Col, Form, Modal } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import { ValidationForm, TextInput, SelectGroup } from 'react-bootstrap4-form-validation';
import callApi from '../../../helpers/conectorApi';
import { alert_exitoso, alert_warning } from '../../../helpers/Notificacion';
import { useForm } from '../../hooks/useForm';
import Loading from '../base/Loading';
import { ValidarNit } from '../../../helpers/ValidarNit';
const menuIdMunicipio = 10;
export const ClienteUpSert = ({ dataInicial, abrirModal, setAbrirModal, GetProveedores, catDepartamento = [] }) => {
    const state = useSelector(state => state);
    const [values, handleOnChange, , setValues] = useForm(dataInicial);
    const [loading, setLoading] = useState(false);
    const [accesos, setAccesos] = useState([]);
    const [departamentoId, setDepartamentoId] = useState('');
    const [readOnlyNombre, setReadOnlyNombre] = useState(false);
    const [catMunicipio, setCatMunicipio] = useState([]);

    const GetAccesosByMenuId = () => {
        if (state?.accesos) {
            const { accesos } = state;
            const misAccesos = accesos.filter(item => item.menuId === menuIdMunicipio);
            setAccesos(misAccesos);
        }
    }
    const handleOnSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        if(String(values.fecha_nacimiento).trim()==="Invalid date"){
            delete values.fecha_nacimiento;
        }
        let response = await callApi('cliente', {
            method: 'POST',
            body: JSON.stringify(values)
        });

        if (response) {
            if(values.personaId>0){
            alert_exitoso("Cliente actualizado exitosamente");
            }else{
                alert_exitoso("Cliente registrado exitosamente");

            }
            setAbrirModal(false);
        }
        setLoading(false);
    }


    const handleErrorSubmit = (e, formData, errorInputs) => {
        alert_warning("Por favor complete toda la información solicitada por el formulario");
    };

    const GetMunicipio = async (id) => {
        if (id > 0) {
            if (accesos.find(acceso => acceso.menuId === menuIdMunicipio && acceso.accesoId === 3)) {
                setLoading(true);
                let response = await callApi(`municipio?departamentoId=${id}&estadoId=1&include=0`);
                if (response) {
                    setCatMunicipio(response);
                }
            } else {
                setCatMunicipio([{ municipioId: '', descripcion: 'No esta autorizado' }]);
            }
        }
        setLoading(false);
    }


    const GetInfoNit = async () => {
        setLoading(true);
        let response = await callApi(`sat/consulta/nit/${values.nit}`);
        if (response) {
            setValues({
                ...values,
                nombre: response.nombre
            });
            setReadOnlyNombre(true);
        }
        setLoading(false);
    }


    useEffect(() => {
        GetAccesosByMenuId();
    }, []);

    useEffect(() => {
        GetMunicipio(values.departamentoId);
    }, [values.departamentoId, accesos]);

    useEffect(() => {
        if (values.nit.trim().length > 0) {
            let expresion = new RegExp("([0-9])[zZ]{0,1}$");
            if (expresion.test(values.nit)) {
                if (ValidarNit(values.nit)) {
                    GetInfoNit();
                };
            } else {
                setReadOnlyNombre(false);
            }
        }
    }, [values.nit]);



    const errorMessage = "Campo obligatorio";
    return (
        <Modal show={abrirModal} onHide={() => setAbrirModal(false)} size="xl">
            {
                loading === true ?
                    <Loading />
                    : <>
                        <Modal.Header closeButton>
                            <Modal.Title as="h5">{dataInicial.personaId > 0 ? `Actualizar Cliente Nit:${values.nit}` : `Nuevo Cliente Nit:${values.nit}`}</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <ValidationForm onSubmit={handleOnSubmit} onErrorSubmit={handleErrorSubmit}>
                                <Form.Row>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="nombre1">Primer Nombre</Form.Label>
                                        <TextInput
                                            name="nombre1"
                                            id="nombre1"
                                            required
                                            value={values.nombre1}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Primer Nombre"
                                            autoComplete="off"
                                            type="text"
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="nombre1">Segundo Nombre</Form.Label>
                                        <TextInput
                                            name="nombre2"
                                            id="nombre2"
                                            value={values.nombre2?values.nombre2:""}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Segundo Nombre"
                                            autoComplete="off"
                                            type="text"
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="apellido1">Primer Apellido</Form.Label>
                                        <TextInput
                                            name="apellido1"
                                            id="apellido1"
                                            value={values.apellido1?values.apellido1:""}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Primer Apellido"
                                            autoComplete="off"
                                            type="text"
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="apellido2">Segundo Apellido</Form.Label>
                                        <TextInput
                                            name="apellido2"
                                            id="apellido2"
                                            value={values.apellido2?values.apellido2:""}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Segundo Apellido"
                                            autoComplete="off"
                                            type="text"
                                        />
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="fecha_nacimiento">Fecha de Nacimiento</Form.Label>
                                        <TextInput
                                            name="fecha_nacimiento"
                                            id="fecha_nacimiento"
                                            value={values.fecha_nacimiento}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Fecha de Nacimiento"
                                            autoComplete="off"
                                            type="date"
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="generoId">Género</Form.Label>
                                        <SelectGroup
                                            name="generoId"
                                            id="generoId"
                                            value={values.generoId}
                                            required
                                            errorMessage={errorMessage}
                                            onChange={handleOnChange}>
                                            <option value="">Seleccione un género</option>
                                            <option value="1">Masculino</option>
                                            <option value="2">Femenino</option>
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="email">Correo</Form.Label>
                                        <TextInput
                                            name="email"
                                            id="email"
                                            value={values.email}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Correo electróncio"
                                            autoComplete="off"
                                            type="email"
                                            style={{ textTransform: 'lowercase' }}
                                        />
                                    </Form.Group>
                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="departamentoId">Departamento</Form.Label>
                                        <SelectGroup
                                            name="departamentoId"
                                            id="departamentoId"
                                            required
                                            value={values.departamentoId}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}>
                                            <option value="">Seleccione un departamento</option>
                                            {
                                                catDepartamento.map(({ departamentoId, descripcion }) => (
                                                    <option value={departamentoId} key={departamentoId}>{descripcion}</option>
                                                )
                                                )
                                            }
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="municipioId">Municipio</Form.Label>
                                        <SelectGroup
                                            name="municipioId"
                                            id="municipioId"
                                            required
                                            value={values.municipioId}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}>
                                            <option value="">Seleccione un municipio</option>
                                            {
                                                catMunicipio.map(({ municipioId, descripcion }) => (
                                                    <option value={municipioId} key={municipioId}>{descripcion}</option>
                                                ))
                                            }
                                        </SelectGroup>
                                    </Form.Group>

                                    <Form.Group as={Col} md="6">
                                        <Form.Label htmlFor="direccion">Direccion</Form.Label>
                                        <TextInput
                                            name="direccion"
                                            id="direccion"
                                            required
                                            multiline
                                            value={values.direccion}
                                            onChange={handleOnChange}
                                            errorMessage={errorMessage}
                                            placeholder="Direccion"
                                            autoComplete="off"
                                            type="text"
                                            rows="1"
                                            minLength="3"
                                        />
                                    </Form.Group>
                                </Form.Row>
                                <Form.Row className="align-items-center m-l-0">
                                    <Col xs={0} xl={8} md={4} />
                                    <Col className="text-right" xs={6} xl={2} md={4}>
                                        <button type="button" onClick={() => { setAbrirModal(false) }} className="btn btn-warning"> Cancelar</button>
                                    </Col>
                                    <Col className="text-right" xs={6} xl={2} md={4}>
                                        <button type="submit" className="btn btn-success"> {dataInicial.personaId > 0 ? 'Actualizar' : 'Registrar'}</button>
                                    </Col>
                                </Form.Row>
                            </ValidationForm>
                        </Modal.Body>
                    </>
            }
        </Modal>
    )
}
